body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-image: radial-gradient( circle 311px at 8.6% 27.9%,  rgba(62,147,252,0.57) 12.9%, rgba(239,183,192,0.44) 91.2% );
  box-sizing: border-box;
}

.container {
 
  display: flex;
  justify-content: space-between;
  height:100%;
  max-width: 900px;
  margin: 10px auto;
  padding: 20px;
  background-color: #fff;
  box-shadow: 3px 3px 10px rgba(151, 141, 141, 0.753);
}

.input-form {
  /* text-align: center; */
 
  background-image: linear-gradient( 109.6deg,  rgba(254,253,205,1) 11.2%, rgba(163,230,255,1) 91.1% );
  flex: 2;
  padding-right: 120px;
}

.input-form label{
  margin-left:12px ;
}

.input-form h1 {
  text-align: center;
  margin-top:10px;
  margin-bottom: 20px;
}
.inp{
  margin-left:10px;
  
  
}
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: bold;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.form-group select {
  height: 38px;
}

.form-group div {
  margin-top: 5px;
}

.form-group div label {
  margin-right: 15px;
}

.output-box {
  flex: 2;
  padding-left: 20px;
  border-left: 1px solid #ccc;
  background-image: linear-gradient( 174.2deg,  rgba(255,244,228,1) 7.1%, rgba(240,246,238,1) 67.4% );
}

.output-box h2 {
  margin-bottom: 20px;
}

.output-box p {
  margin-bottom: 5px;
}

button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin:12px;
}

button:hover {
  background-color: #45a049;
}

.uploaded-image {
  max-width: 200px;
  margin-top: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
}
